@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,*::before,*::after{box-sizing:border-box}html,p,h1,h2,h3,h4,button,ul,a{padding:0;margin:0}html{font-size:100%;color:#b3b3b3;overflow:hidden;height:100%}body{height:100%;overflow:auto}h1,h2,h3,h4{color:#fff}a{cursor:pointer;text-decoration:none;color:inherit}li{list-style-type:none}.outerWrap{height:100vh;font-family:'Montserrat', sans-serif}.app{display:flex;flex-direction:row;flex-wrap:nowrap;width:100%;background:linear-gradient(to bottom, #212121, #131313)}.navBar{position:-webkit-sticky;position:sticky;top:0;display:flex;flex-direction:column;flex-shrink:0;min-width:240px;height:calc(100vh - 90px);background:#040404}.navBar .logo{padding:1.5rem 1.5rem 0;margin-bottom:1.125rem}.navBar .logo svg{height:40px;max-width:130px}.navBar ul{padding:0 0.5rem;margin:0}.navBar ul li{display:flex;align-items:center;height:40px;padding:0 1rem;border-radius:4px;font-size:0.875rem;font-weight:700;color:#b3b3b3;transition:all 300ms ease-in-out}.navBar ul li:hover{color:#fff;cursor:pointer}.navBar ul li.active{background:#212121;color:#fff}.navBar ul li.active svg{fill:#fff}.navBar ul li svg{fill:currentColor;margin-right:1rem}.navBar .cookies{font-size:0.625rem;padding:0 1.5rem;margin:auto 0 2rem;display:flex;flex-direction:column;justify-content:center;align-items:flex-start}.navBar .cookies a{height:25px;cursor:pointer}.navBar .cookies a:hover{color:#fff;text-decoration:underline}.body{width:100%}.header{z-index:1;display:flex;align-items:center;justify-content:space-between;position:-webkit-sticky;position:sticky;top:0;padding:1rem;-webkit-padding-end:16px;padding-inline-end:16px;width:100%;min-width:250px;max-width:calc(100vw - 240px);height:60px;background-color:#090909}.header .buttons{min-width:250px}.header button{padding:0.5rem 2.125rem;height:38px;border-radius:25px;border:2px;font-weight:600;letter-spacing:1px;cursor:pointer}.header button:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}.header :first-child{background-color:transparent;color:#fff}.main{overflow-y:scroll}.card__section{padding:1.5rem 1rem 0;margin-bottom:2rem}.card__section-header{display:flex;flex-direction:row;flex-wrap:nowrap;align-items:flex-end;margin-bottom:1rem;flex:1 0}.card__section-header h2{font-size:1.5rem;font-weight:600;flex:1 0}.card__section-header h2 a{cursor:pointer}.card__section-header h2 a:hover{text-decoration:underline}.card__section-header span{font-size:0.75rem;font-weight:bold;color:#b3b3b3}.card__section-header span:hover{text-decoration:underline;cursor:pointer}.card__section-title{display:flex;flex-direction:column;flex:1 0}.card__section-title p{display:block;font-size:0.875rem;font-weight:400}.playlists{display:grid;grid-gap:1.5rem;grid-template-columns:repeat(auto-fill, minmax(180px, 1fr))}.card{padding:1rem;background-color:#181818;border-radius:4px;transition:all 250ms ease-in-out}.card:hover{background-color:#272727}.card:hover .card__play{top:-4rem;opacity:1}.card__image{margin-bottom:1rem;width:auto;height:auto;box-shadow:0 8px 24px rgba(0,0,0,0.5);border-radius:2px}.card img{width:100%;height:100%}.card__content{position:relative}.card__title{font-size:1rem;margin-bottom:0.25rem}.card__desc{font-size:0.875rem;text-overflow:ellipsis;overflow:hidden;display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical}.card__play{position:absolute;top:-3.5rem;right:0.5rem;opacity:0;transition:all 250ms ease-in-out}.card__play button{width:40px;height:40px;background-color:#1bb954;border:none;border-radius:50%;display:flex;align-items:center;justify-content:center}.card__play button svg{fill:currentColor;color:white}.card__play button:hover{cursor:pointer;-webkit-transform:scale(1.05);transform:scale(1.05);visibility:visible}.playlist__page{display:flex;flex-direction:column;position:absolute;top:0;width:100%;height:100%;overflow-y:scroll;color:rgba(255,255,255,0.7)}.playlist__info{display:flex;flex-direction:row;align-items:flex-end;width:calc(100vw - 240px);height:100%;min-height:340px;padding:0 1rem 2rem;text-align:left;background-color:#b099c5;background-image:linear-gradient(to bottom, transparent 0, rgba(0,0,0,0.5) 100%)}.playlist__image{width:232px;height:232px;margin:0 1.5rem 0 0.75rem;box-shadow:0 4px 60px rgba(0,0,0,0.5)}.playlist__image img{width:100%;height:100%}.playlist__desc{display:flex;flex-direction:column;min-width:450px}.playlist__supratitle{margin-top:0.25rem;font-size:0.75rem;font-weight:700}.playlist__title{padding:0.5rem 0;font-size:3rem}.playlist__subtitle{font-size:0.875rem}.playlist__subtitle,.playlist__stats{margin-bottom:0.8rem}.playlist__stats{font-size:0.875rem}.playlist__stats>*{display:inline-block}.playlist__stats a :hover{text-decoration:underline}.playlist__stats span:before{content:'â€¢';margin:0 4px;font-weight:700}.playlist__tracklist{background-color:#b099c5;background-image:linear-gradient(to bottom, rgba(0,0,0,0.6) 0, #121212 20%)}.playlist__icons{padding:1.5rem 1rem;display:flex;flex-direction:row;align-items:center}.playlist__icons>*{cursor:pointer}.playlist__icons .play-icon{width:56px;height:56px}.playlist__icons :not(:last-child){margin-right:2rem}.playlist__tracks{margin-bottom:0.5rem;padding:1rem}.playlist__tracks li{display:grid;grid-template-columns:16px 4fr 2fr minmax(120px, 1fr);padding:0 1rem;grid-gap:1rem;font-size:0.75rem;letter-spacing:1px}.track{height:56px;align-items:center;font-weight:400}.track:hover{background-color:rgba(255,255,255,0.1);border-radius:0.25rem}.track:hover .track-number span{visibility:hidden}.track:hover .track-number svg,.track:hover .track-runtime svg{visibility:visible}.track-header{display:grid;grid-template-columns:16px 4fr 2fr minmax(120px, 1fr);padding:0 1rem;grid-gap:1rem;align-items:center;margin-bottom:1rem;height:36px;border-bottom:1px solid rgba(255,255,255,0.1);font-size:0.75rem;letter-spacing:1px}.track-header :first-child{display:flex;justify-content:center;align-items:center}.track-number svg{position:absolute;color:#fff;width:16px;height:16px;visibility:hidden}.track-number,.track-runtime{display:flex;flex-direction:row;align-items:center;justify-content:center}.track-cover{width:40px;height:40px;margin-right:1rem}.track-title{display:flex}.track-title p,.track-title span{padding-right:0.5rem}.track-title :first-child{grid-area:title}.track-title :nth-child(2){grid-area:artist}.track-title span{color:#b3b3b3}.track-title span:hover{color:#fff;text-decoration:underline;cursor:pointer}.track-info{display:grid;grid-template:'title title' 'artist artist' / auto 1fr;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;color:#fff}.track-album{display:flex;align-items:center}.track-album:hover{cursor:pointer;text-decoration:underline;color:#fff}.track-runtime{display:flex;flex-direction:row;justify-content:flex-end}.track-runtime>:not(:last-child){margin-right:1rem}.track-runtime svg{width:16px;height:16px;visibility:hidden}.track-runtime svg:hover{color:#fff}.play-icon{width:40px;height:40px;background-color:#1bb954;border:none;border-radius:50%;display:flex;align-items:center;justify-content:center}.play-icon svg{fill:currentColor;color:white}.play-icon:hover{cursor:pointer;-webkit-transform:scale(1.05);transform:scale(1.05)}.like-icon,.ellipsis-icon{width:32px;height:32px;background-color:transparent;border:none;color:inherit}.like-icon:hover,.ellipsis-icon:hover{color:#fff}.clock-icon{display:flex;justify-self:flex-end;margin-right:2rem}.musicControls{position:-webkit-sticky;position:sticky;bottom:0;display:flex;align-items:center;justify-content:center;height:90px;background:#212121;border-top:1px solid #282828;text-align:center;color:#fff}

